const EPSILON = 0.00000001;
/**
 * 
 * @param {!number|string|null} data      null
 * @param {?number} precision             null2
 * @param {?''|'K'|'M'|'B'|'K/M'|'K/M/B'|''|''|'/'|'%'} unit    null''
 * @param {boolean|string=} useDefault    --
 * @param {boolean=} isAutoPrec    true 
 * @param {boolean=} isAbs    
 * @returns {string}
 */
const formatStockText = function formatStockText(
  data,
  precision,
  unit,
  useDefault,
  isAutoPrec,
  isAbs
) {
  let n = Number(data);
  if (
    (data == null || isNaN(n) || n === Infinity || n === -Infinity) &&
    useDefault !== false
  ) {
    return typeof useDefault === 'string' ? useDefault : '--';
  }

  const abs = Math.abs(n);
  const m = n;

  unit = unit || '';
  precision = precision != null ? precision : 2;
  if (unit) {
    if (unit.indexOf('B') >= 0 && abs >= 1000000000) {
      unit = 'B';
      n /= 1000000000;
    } else if (unit.indexOf('') >= 0 && abs >= 1000000000000) {
      unit = '';
      n /= 1000000000000;
    } else if (unit.indexOf('') >= 0 && abs >= 100000000) {
      unit = '';
      n /= 100000000;
    } else if (unit.indexOf('M') >= 0 && abs >= 1000000) {
      unit = 'M';
      n /= 1000 * 1000;
    } else if (unit.indexOf('') >= 0 && abs >= 10000) {
      unit = '';
      n /= 10000;
    } else if (unit.indexOf('K') >= 0 && abs >= 1000) {
      unit = 'K';
      n /= 1000;
    } else if (unit === 100) {
      unit = '';
      n /= 100;
    } else if (unit === '%') {
      n *= 100;
    } else {
      unit = '';
    }
  }

  if (n === m && isAutoPrec === true) {
    precision = 0;
  }
  if (isAbs) {
    n = Math.abs(n);
  }

  return (n + EPSILON).toFixed(precision) + unit;
};

/**
 * 
 * @param {any} price 
 * @param {number} [precision=2]  
 * @param {boolean} [withSign=false]  
 * @returns
 */
const formatPrice = function(
  price,
  precision = 2,
  withSign = false
) {
  //  null 0
  // if (withSign && price > 0) {
  //   return '+' + formatStockText(price, precision)
  // }
  return formatStockText(price, precision);
};

/**
 * 
 * @param {any} value 
 * @param {number} [precision=2] 
 * @param {boolean} [withSign=true]  
 * @param {number} [ratio=100] 
 * @returns
 */
const formatRatio = function formatRatio(
  value,
  precision = 2,
  withSign = true,
  ratio = 100
) {
  if (!ratio) ratio = 1;
  if (value != null) {
    value = value / ratio;// null0
  }
  if (withSign && value > 0) {
    return '+' + formatStockText(value, precision);
  }
  return formatStockText(value, precision);
};

/**
 * 
 * @param {any} value 
 * @param {number} [compareValue=0]  
 * @param {string} [style=['', 'stock-up', 'stock-down']] 
 * @returns
 */
const formatColor = function formatColor(
  value,
  compareValue = 0,
  style = ['', 'stock-up', 'stock-down']
) {
  if (!style || style.length <= 2) {
    return '';
  }

  if (value > compareValue) return style[1];
  else if (value < compareValue) return style[2];
  return style[0];
};
/**
 * 
 * @param {any} volume  
 * @param {any} unit  
 * @param {number} [precision=2] 
 * @returns
 */
const formatVolume = function formatVolume(volume, unit = 100, precision = 2) {
  if (!unit) unit = 1;
  if (volume != null) {
    volume = volume / unit; // null0
  }
  return formatStockText(volume, precision, '', true, true);
};

/**
 * 
 * 
 * @param {Function} func 
 * @param {Number} wait 
 * @param {Boolean} immediate 
 */
export function debounce(func, wait, immediate) {
  let timeout;
  return function() {
    let context = this;
    let args = arguments;
    let later = function() {
      timeout = null;
      if (!immediate) {
        func.apply(context, args);
      }
    };
    let callNow = immediate && !timeout;
    window.clearTimeout(timeout);
    timeout = window.setTimeout(later, wait);
    if (callNow) {
      func.apply(context, args);
    }
  };
}

/**
 * 
 *  wait 
 *  mustRun  func
 * @param {Function} func 
 * @param {Number} wait ms
 * @param {Number} mustRun msmustRun > wait
 */
export function throttle(func, wait, mustRun) {
  let timeout;
  let startTime = new Date();
  return function() {
    let context = this;
    let args = arguments;
    let curTime = new Date();
    window.clearTimeout(timeout);
    if (curTime - startTime >= mustRun) {
      //  
      func.apply(context, args);
      startTime = curTime;
    } else {
      // 
      timeout = window.setTimeout(func, wait);
    }
  };
}

// 
export function oneOf(value, validList) {
  for (let i = 0; i < validList.length; i++) {
    if (value === validList[i]) {
      return true;
    }
  }
  return false;
}
export function getKeyFromParams(params) {
  let { obj, gql, market, selfStock } = params || {};
  let key = '';
  if (gql) {
    key = gql.split('/')[2];
  }
  if (selfStock) {
    key = 'selfStock';
  }
  return key;
}
export function keyUnlock(params) {

}

export default { formatPrice, formatStockText, formatColor, formatRatio, formatVolume, debounce, throttle, oneOf, getKeyFromParams, keyUnlock };
