import largeList from './components/large-list';
import smallList from './components/small-list';
import staticSmallList from './components/static-small-list';
import Utils from './utils/index.js';
// 
const components = [
  largeList, smallList, staticSmallList,
];

//  install  Vue  use 
const install = function(Vue) {
  // 
  if (install.installed) return;
  // 
  components.map(component => Vue.component(component.name, component));

  Vue.prototype.$Utils = Utils;
  Vue.Utils = Utils;
};

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  //  install Vue.use() 
  install,
  // 
  largeList,
  smallList,
  staticSmallList,
};
